.ulMenu{
    list-style-type: none;
    position: relative;
}

.listMenu{
    list-style-type: none;
    position: relative;
}
.listAnchor{
    font-size: 1.0rem;
    font-weight: 500;
    color: #FFF;
    text-decoration: none;
    cursor: pointer;
}
.listAnchorSpan{
    font-size: 1.0rem;
    font-weight: 500;
    color: #FFF;
    text-decoration: none;
    cursor: pointer;
}

.drpDwnMnu{
    display: none;
}
.listAnchorSpan:hover .drpDwnMnu{
    position: absolute;
    background: white;
    color: black;
    top: 45px;
    align-items: center;
    display: flex;
}
.iqLogo{
	width: 100px;
	height: 100px;
	border-radius: 50%;
	padding: 15px;
}
